<template>
  <div>
    <Header/>
    <Navigation position="nav"/>
    <Category
        v-for="item in store.state.main.itemArr"
        :item="item"/>
    <SelectOption
        v-if="store.state.main.isOpen"/>
    <GoToCart
        v-if="store.state.main.hasCart > 0"/>
    <Footer/>
  </div>
</template>

<script>
import Header from "@/components/layout/Header";
import Category from "@/components/layout/Category";
import SelectOption from "@/components/template/popup/SelectOption";
import {useStore} from "vuex";
import GoToCart from "@/components/template/popup/GoToCart";
import Navigation from "@/components/template/head/Navigation";
import Footer from "@/components/layout/Footer";

export default {
  name: "MainContainer",
  components: {Footer, GoToCart, Category, Header, SelectOption, Navigation},
  setup() {
    const store = useStore()

    const setNavState = (offset) => {
      const temp = []
      document.querySelectorAll('.category').forEach(item => {
        temp.push(item.offsetTop)
      })
      store.commit('main/setCategoryOffset', temp)
      const arr = Array.from(store.state.main.categoryOffset)

      for(let i = 0; i < arr.length; i++) {
        if(offset <= arr[0]
            || (offset >= arr[i] && offset < arr[i + 1])
        ) {
          store.commit('main/changeNav', store.state.main.itemArr[i].category)
          break;
        } else if(i === arr.length - 1) {
          store.commit('main/changeNav', store.state.main.itemArr[i].category)
          break;
        }
      }
    }

    store.dispatch('main/getItemArr')
    const cart = JSON.parse(sessionStorage.getItem('cart')).length
    store.commit("main/setHasCart", cart)
    window.addEventListener('scroll', () => {
      setNavState(window.scrollY)
    })


    return { setNavState, store }
  }
}
</script>

