<template>
  <div class="main_cart_wrap" v-if="store.state.main.hasCart > 0">
    <Header />
    <Navigation position="cart"/>
    <div class="main-contents-wrap">
      <Cart/>
      <TotalPrice/>
    </div>
  </div>
  <div v-else>
    <Header />
    <Navigation position="cart"/>
    <EmptyCart/>
  </div>
</template>

<script>
import Header from "@/components/layout/Header";
import Navigation from "@/components/template/head/Navigation";
import Cart from "@/components/layout/Cart";
import TotalPrice from "@/components/template/cart/TotalPrice";
import EmptyCart from "@/components/template/cart/EmptyCart";
import {useStore} from "vuex";
import {ref} from "vue";

export default {
  name: "CartContainer",
  components: {TotalPrice, Cart, Navigation, Header, EmptyCart},
  setup() {
    const store = useStore()
    store.commit('main/setHasCart', JSON.parse(sessionStorage.getItem('cart')).length)
    return { store }
  }
}
</script>

<style scoped>
.main_cart_wrap {
  min-height: 100vh;
}

.main-contents-wrap {
}
</style>