<template>
  <div class="popup_container">
    <div class="inner">
      <img class="close" src="@/assets/img/closeBtn.svg" alt="close button" @click="closePopup">
      <img class="item-img" :src="require('@/assets/' + popupData.image)" alt="item image">
      <h3 class="item-name">
        <span>{{popupData.title}}</span>
        <div>
          <span>수량</span>
          <div class="inp-wrap">
            <button @click="downCnt"><img src="@/assets/img/minusBtn.svg" alt="minusBtn"></button>
            <input type="number" :value="popupData.count">
            <button @click="upCnt"><img src="@/assets/img/plusBtn.svg" alt="plusBtn"></button>
          </div>
        </div>
      </h3>

      <ul class="option-list">
        <li class="option" v-for="item in popupData.option">

          <div class="temperature" v-if="item.type === 'temperature' && item.name.length > 1">
            <button @click="setTemperature('hot')" :class="temperature.hot">{{ item.name[0] }}</button>
            <button @click="setTemperature('ice')" :class="temperature.ice">{{ item.name[1] }}</button>
          </div>

          <div class="temperature" v-if="item.type === 'temperature' && item.name.length === 1">
            <button class="choose">{{ item.name[0] }}</button>
          </div>

          <div class="count" v-else-if="item.type === 'count'">
            <span>{{item.name}}</span>
            <div class="inp-wrap">
              <button @click="decrease(item)"><img src="@/assets/img/minusBtn.svg" alt="minusBtn"></button>
              <input type="number" :value="item.value" :min="item.min" :max="item.max">
              <button @click="increase(item)"><img src="@/assets/img/plusBtn.svg" alt="plusBtn"></button>
            </div>
          </div>

          <div class="button" v-else-if="item.type === 'button'">
            <span>{{item.name}}</span>
            <button @click="addSyrup(item)" v-if="item.value === 0">추가</button>
            <button @click="delSyrup(item)" :style="{background: '#c7c7c7'}" v-if="item.value === 1">취소</button>
          </div>

        </li>
      </ul>
      <div class="price">
        <span>금액</span>
        <span>{{ popupData.totalPrice }} 원</span>
      </div>
      <div class="btn-wrap">
        <button @click="addCart">장바구니</button>
        <button @click="goToCart">바로 구매</button>
      </div>
    </div>
  </div>
</template>

<script>
import {useStore} from "vuex";
import {ref} from "vue";
import {useRouter} from "vue-router";

export default {
  name: "SelectOption",
  setup() {
    const store = useStore()
    const router = useRouter()

    const jsonPopup = JSON.parse(JSON.stringify(store.state.main.popupData))

    const popupData = ref(jsonPopup)
    const temperature = ref({ hot: 'choose', ice: '' })
    const originData = ref(jsonPopup)
    const totalPrice = ref(0)

    const closePopup = () => {
      popupData.value = originData.value
      store.commit('main/setIsOpen', false)
    }

    const decrease = (item) => {
      if(item.value > item.min) {
        item.value--
        popupData.value.totalPrice -= (item.price * popupData.value.count)
      }
    }

    const increase = (item) => {
      if(item.value < item.max) {
        item.value++
        popupData.value.totalPrice += (item.price * popupData.value.count)
      }
    }

    const addCart = () => {
      const obj = {
        idx: store.state.main.hasCart,
        image: popupData.value.image,
        title: popupData.value.title,
        price: popupData.value.totalPrice,
        temperature: temperature.value.hot === '' ? 'ice' : 'hot',
        count: popupData.value.count,
        option: []
      }

      popupData.value.option.forEach(item => {
        obj.option.push({...item})
      })

      if(sessionStorage.getItem("cart") === null) {
        sessionStorage.setItem("cart", JSON.stringify([obj]))
      } else {
        const temp = JSON.parse(sessionStorage.getItem("cart"))
        temp.push(obj)
        sessionStorage.setItem("cart", JSON.stringify(temp))
      }

      store.commit('main/setHasCart', JSON.parse(sessionStorage.getItem("cart")).length)
      popupData.value = originData.value
      store.commit('main/setIsOpen', false)
    }

    const setTemperature = (temper) => {
      switch(temper) {
        case 'hot':
          temperature.value = {hot: "choose", ice: ""}
          break;
        case 'ice':
          temperature.value = {hot: "", ice: "choose"}
          break;
      }
    }

    const addSyrup = (item) => {
      item.value = 1
      popupData.value.totalPrice += (item.price * popupData.value.count)
    }

    const delSyrup = (item) => {
      item.value = 0
      popupData.value.totalPrice -= (item.price * popupData.value.count)
    }

    const goToCart = () => {
      addCart()
      router.push('/cart')
    }

    const downCnt = () => {
      if(popupData.value.count > 0) {
        popupData.value.count--
        popupData.value.totalPrice -= popupData.value.price
      }
    }

    const upCnt = () => {
      popupData.value.count++
      popupData.value.totalPrice += popupData.value.price
    }

    totalPrice.value = popupData.value.price

    return {
      popupData, temperature, originData, totalPrice,
      closePopup, decrease, increase, addCart, setTemperature, addSyrup, delSyrup,
      goToCart, downCnt, upCnt
    }
  }
}
</script>

