<template>
  <div class="wrap">
    <button @click="goToCart">장바구니 {{store.state.main.hasCart}}개 상품</button>
  </div>
</template>

<script>
import {useRouter} from "vue-router";
import {useStore} from "vuex";

export default {
  name: "GoToCart",
  setup() {
    const router = useRouter()
    const store = useStore()

    const goToCart = () => {
      router.push('/cart')
    }

    return { goToCart, store }
  }
}
</script>

<style scoped>
.wrap {
  position: fixed;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  padding: 2vw 5vw;
  width: 100%;
}

button {
  display: block;
  width: 100%;
  background: #FFAB6F;
