const cart = {
  namespaced: true,
  state: {
    cartItem: [],
    totalPrice: 0,
    productCnt: 0
  },
  mutations: {
    setCartItem: (state) => {
      const temp = JSON.parse(sessionStorage.getItem("cart"))
      state.cartItem = [...temp]
    },
    setTotalPrice: (state, price) => {
      state.totalPrice = price
    },
    setProductCnt: (state, cnt) => {
      state.productCnt = cnt
    }
  },
  actions: {

  }
}

export default cart;