<template>
  <h1>
    <span>{{ storeName }}</span>
    <button @click="goLogin">로그인</button>
  </h1>
</template>

<script>
import {ref} from "vue";
import {useRoute, useRouter} from "vue-router";

export default {
  name: "Title",
  setup() {
    const router = useRouter()
    const route = useRoute()
    const storeName = ref("");

    const goLogin = () => {
      router.push('/login')
    }

    if(route.params.id > 0) {
      storeName.value = '커피공장'
    } else {
      storeName.value = '메가커피'
    }

    return { storeName, goLogin }
  }
}
</script>

<style scoped>
h1 {
  font-size: 5vw;
  color: #FFAB6F;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  color: #ffffff;
  background: #FFAB6F;
  border-radius: 4px;
  padding: .5vw 2vw;
}
</style>