<template>
  <div class="item" @click="openPopup">
    <img :src="require('@/assets/' + detail.image)" alt="item image">
    <div class="title">{{ detail.title }}</div>
    <div class="price">{{ detail.price }}</div>
  </div>
</template>

<script>
import {useStore} from "vuex";
import {ref} from "vue";

export default {
  name: "CategoryItem",
  props: ["_detail"],
  setup(props) {

    const store = useStore()
    const detail = ref({...props._detail})

    const openPopup = () => {
      store.commit('main/setPopupData', {...detail.value})
      store.commit('main/setIsOpen', true)
    }

    return { detail, openPopup }
  }
}
</script>

<style scoped>
.item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2vw 0;
  border-radius: 8px;
  background: #ffffff;
  box-shadow: #00000020 2px 2px 4px;
}

img {
  width: 50%;
  margin-bottom: 10px;
}

.title {
  margin-bottom: 5px;
  font-size: 3vw;
}

.price {
  font-size: 2.5vw;
}
</style>