<template>
    <router-view/>
</template>

<script>

import {useStore} from "vuex";

export default {
  name: 'App',
  setup() {
    const store = useStore()
    if(sessionStorage.getItem("cart") === 'undefined' || sessionStorage.getItem("cart") === null) {
      sessionStorage.setItem('cart', '[]')
    } else {
      store.commit('main/setHasCart', sessionStorage.getItem('cart').length)
    }
  }
}
</script>

<style>
#app {
  background: #F7F7F7;
  min-height: 100vh;
}
</style>
