<template>
  <div class="price_wrap">
    <div class="price">
      <span class="text">총 주문 금액</span>
      <span>{{ store.state.cart.totalPrice }} 원</span>
    </div>
    <button class="buy-btn">{{ store.state.cart.productCnt }}개 상품 구입</button>
  </div>
</template>

<script>
import {useStore} from "vuex";

export default {
  name: "TotalPrice",
  setup() {
    const store = useStore()
    let totalPrice = 0
    let productCnt = 0

    const cart = JSON.parse(sessionStorage.getItem('cart'))
    cart.forEach(item => {
      totalPrice += item.price
      productCnt += item.count
    })

    store.commit('cart/setTotalPrice', totalPrice)
    store.commit('cart/setProductCnt', productCnt)

    return { store, totalPrice, productCnt }
  }
}
</script>

