import axios from "axios";

const main = {
  namespaced: true,
  state: {
    itemArr: [],
    popupData: {},
    isOpen: false,
    hasCart: 0,
    categoryOffset: []
  },
  mutations: {
    setItemArr: (state, itemArr) => {
      state.itemArr = itemArr
    },
    setPopupData: (state, item) => {
      state.popupData = item
    },
    setIsOpen: (state, flag) => {
      state.isOpen = flag
    },
    setHasCart: (state) => {
      const tempArr = JSON.parse(sessionStorage.getItem('cart'))
      let cnt = 0
      tempArr.forEach(item => {
        cnt += item.count
      })
      state.hasCart = cnt
    },
    setCategoryOffset: (state, offset) => {
      state.categoryOffset = offset
    },
    changeNav: (state, item) => {
      for (const i in state.itemArr) {
        state.itemArr[i].class = ""
        if(state.itemArr[i].category === item) {
          state.itemArr[i].class = 'choose'
        }
      }
    }
  },
  actions: {
    getItemArr(context) {
      axios
        .get('/dummyData/testData.json')
        .then(res => {
          context.commit("setItemArr", {...res.data})
        })
    }
  }
}

export default main;