<template>
  <article>
    <img class="item-img" :src="require('@/assets/' + item.image)" alt="img">
    <button @click="delItem(item)" class="close-btn"><img src="@/assets/img/closeBtn.svg" alt="closeBtn"></button>
    <div class="info">
      <span class="item-name">{{item.title}}</span>
      <ul class="option">
        <li v-for="option in item.option">
          <span v-if="option.value > 0">- {{option.name}} : {{option.value}}</span>
          <span v-else-if="option.type === 'temperature'">- {{item.temperature}}</span>
        </li>
      </ul>
      <div class="bottom-wrap">
        <div class="cnt">
          <span>수량</span>
          <div class="cnt-btn-wrap">
            <button @click="decreaseCnt(item)"><img src="@/assets/img/minusBtn.svg" alt="minusBtn"></button>
            <input type="number" :value="item.count">
            <button @click="increaseCnt(item)"><img src="@/assets/img/plusBtn.svg" alt="plusBtn"></button>
          </div>
        </div>
        <div class="price">{{ item.price }} 원</div>
      </div>
    </div>
  </article>
</template>

<script>
import {useStore} from "vuex";

export default {
  name: "Card",
  props: ['item'],
  setup() {
    const store = useStore()

    const delItem = (item) => {
      const originCart = Array.from(JSON.parse(sessionStorage.getItem('cart')))
      const idx = originCart.indexOf(originCart[item.idx])
      originCart.splice(idx, 1)
      sessionStorage.setItem('cart', JSON.stringify(originCart))
      store.commit('main/setHasCart', originCart.length)
      store.commit('cart/setCartItem')
      refresh()
    }

    const decreaseCnt = (item) => {
      if(item.count > 1) {
        item.price -= item.price / item.count
        item.count--
        const cart = JSON.parse(sessionStorage.getItem('cart'))
        const temp = cart.find(sessionCart => sessionCart.idx === item.idx)
        temp.price -= temp.price / temp.count
        temp.count--
        sessionStorage.setItem('cart', JSON.stringify(cart))
        refresh()
      }
    }

    const increaseCnt = (item) => {
      item.price += item.price / item.count
      item.count++
      const cart = JSON.parse(sessionStorage.getItem('cart'))
      const temp = cart.find(sessionCart => sessionCart.idx === item.idx)
      temp.price += temp.price / temp.count
      temp.count++
      sessionStorage.setItem('cart', JSON.stringify(cart))
      refresh()
    }

    const refresh = () => {

      let totalPrice = 0
      let productCnt = 0
      const cart = JSON.parse(sessionStorage.getItem('cart'))
      cart.forEach(item => {
        totalPrice += item.price
        productCnt += item.count
      })

      store.commit('cart/setTotalPrice', totalPrice)
      store.commit('cart/setProductCnt', productCnt)
    }

    return { delItem, decreaseCnt, increaseCnt }
  }
}
</script>

