<template>
  <div class="box">
    <div class="box-name" @click="dropOpen">
      <span>적립</span>
      <button>
        <img
            src="@/assets/img/dropArrow-orange.svg"
            alt="dropArrow"
            :style="{transform:rotate}"
        />
      </button>
    </div>
    <div class="content-wrap">
      <div class="log-in-content" v-if="isLogin && isOpen">
        Kade님 포인트 <span>20점</span> 적립 예정입니다.
      </div>
      <div class="log-out-content" v-if="!isLogin && isOpen">
        <label>
          <span>핸드폰 번호</span>
          <input type="tel" placeholder="ex) 01012345678">
        </label>
      </div>
    </div>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "SaveCoupon",
  setup() {
    const isLogin = ref(true)
    const isOpen = ref(false)
    const rotate = ref('rotate(0)')

    const dropOpen = () => {
      isOpen.value = !isOpen.value
      rotate.value = isOpen.value ? 'rotate(180deg)' : 'rotate(0)'
    }

    return { isLogin, isOpen, rotate, dropOpen }
  }
}
</script>

