<template>
  <nav v-if="position === 'nav'">
    <ul>
      <li
          v-for="item in store.state.main.itemArr"
          :class="item.class"
          @click="changeNav(item)">
        <a :href="`#${item.category}`">{{item.category}}</a>
      </li>
    </ul>
  </nav>
  <div class="box" v-else-if="position === 'cart'">
    <span>장바구니</span>
    <button @click="prevBtn"><img src="@/assets/img/backArrow.svg" alt="backBtn"></button>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { useRouter } from "vue-router";

export default {
  name: "Navigation",
  props: ["position"],
  setup(props) {
    const router = useRouter()
    const store = useStore()

    const changeNav = (item) => {
      store.commit('main/changeNav', item)
    }
    const prevBtn = ()=> {
      router.back()
    }

    return { changeNav, prevBtn, store }
  }
}
</script>

